import React from "react";



class BrickCatMenu extends React.Component {
    constructor(props) {
        super(props);
    }


    componentDidUpdate() {

    }


    newGameInit(newState) {
        this.props.updateState(newState);
    }


    render() {
        if (window.innerWidth < 1200) {
            return (<div className="menuWrapper" ><div>    .</div></div>)
        } else {
            return (
                <div>
                    <div className="menuWrapper" >
                        <div className="menuHeader">  !!!</div>
                        <div className="menuContent">
                            <div>     </div>
                            <div>!   !</div>
                            <div className="flex-column center">
                                <div className="columt-element">
                                    <button onClick={() => this.newGameInit("newGame-everlasting")} className="button">  ,         18 </button>
                                </div>
                                <div className="columt-element">
                                    <button onClick={() => this.newGameInit("newGame-shinda")} className="button">,     </button>
                                </div>
                                <div className="columt-element">
                                    <button onClick={() => this.newGameInit("newGame-alecHolowka")} className="button">     ,    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="faq">
                        <button onClick={() => this.newGameInit("faq")} className="button"> </button>
                    </div>
                </div>
            );
        }

    }
}

export default BrickCatMenu;