

import React from "react";
import './index.css';

class BrickCatGameWin extends React.Component {
    constructor(props) {
        super(props);

    }

    componentDidMount() {

    }
    newGameInit() {
        this.props.updateState("menuPage");
    }

    

    render() {
        return (
            <div className={this.props.newState == 'gameWinIgorAlive' ? "igorAlive gameField" : "gameField"}>
                <div className="endTextWrapper">
                    <div className="endText">
                        <div>{this.props.newState == "gameWinIgorAlive" ? "!      !!" : "!   c      !"}</div>
                        <div>{this.props.newState == "gameWinIgorAlive" ? "       .  Catwar     ." : "      catwar    ."}</div>
                        <button onClick={() => this.newGameInit()} className="button">  </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default BrickCatGameWin;
